
.cart{
}
.cart-bg{
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.6);
  z-index: 10;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.cart-content{
  position: fixed;
  bottom: 0px;
  font-size: 0;
  left:0px;
  width: 100%;
  height: 48px;
  z-index: 50;
  background: #141d24;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cart-content .cart-left{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 48px;
}
.cart-content .cart-left .icon-wrapper{
   position: relative;
   display: inline-block;
   top:-10px;
   left: 0px;
   margin:0px 12px;
   padding: 6px;
   height: 56px;
   width: 56px;
   border-radius: 50%;
