}
.cart-view{
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 40;
}
.cart-view .cart-main{
  width: 100%;
  max-height: 305px;
  overflow: hidden;
  background: #fff;
}
.cart-title{
  height: 40px;
  width: 100%;
  background: #f3f5f7;
  position: relative;
}
.cart-title:after{
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: 1px;
  width: 100%;
  transform: translateY(0.5);
