}
.cart-view .cart-main .cart-des .food-item{
  position: relative;
  height: 48px;
  line-height: 48px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cart-view .cart-main .cart-des .food-item:after{
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: 1px;
