}
.cart-view .cart-main .cart-des .food-item .food-name{
  font-size: 14px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 48px;
  color: rgb(7,17,27);
}
.cart-view .cart-main .cart-des .food-item .food-price{
  font-size: 14px;
  line-height: 48px;
  vertical-align: top;
  padding:0px 12px 0px 18px;
  font-weight: 700;
  color: rgb(240,20,20);
}
.cart-view .cart-main .cart-des .food-item .food-control{
  margin-top: 9px;
  line-height: 48px;
  height: 48px;
}



