
.cartcontrol{
  font-size: 0;
  color: rgb(0,160,222);
}
.add-wrapper{
  font-size: 24px;
  padding: 6px;
  line-height: 24px;
  display: inline-block;
}
.cart-text{
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: rgb(147,153,159);
  line-height: 24px;
  width: 12px;
  padding-top: 6px;
  text-align: center;
}
.decrease-wrapper{
  font-size: 24px;
  padding: 6px;
  line-height: 24px;
  display: inline-block;
}



