
.star{
	font-size: 0;
}
.star .star-item{
	display: inline-block;
	margin:0px 8px;
}
.star-24 .half{
	height: 20px;
	width: 20px;
	background: url('star24_half@2x.png') no-repeat;
	background-size: 20px 20px;
}
.star-24 .off{
	height: 20px;
	width: 20px;
	background: url('star24_off@2x.png') no-repeat;
	background-size: 20px 20px;
}
.star-24 .on{
	height: 20px;
	width: 20px;
	background: url('star24_on@2x.png') no-repeat;
	background-size: 20px 20px;
}



