
.good{
	position: absolute;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	overflow: hidden;
	width: 100%;
	top: 174px;
	bottom:46px;
}
.good .goods-left{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 80px;
	        flex: 0 0 80px;
	width: 80px;
	background:#f3f5f7;
}
.good .goods-left.current{
	background:rgb(240,20,20);
}
.good .goods-left .goods{
	font-size: 12px;		
	font-weight: 200;
}
.good .goods-left .goods .goods-item{
	display: table;
	width: 80px;
	height: 54px;
	line-height: 14px;
}
.good .goods-left .goods .current{
	background:#fff;
}
.good .goods-left .goods .goods-item.border-line{
	position: relative;
}
.good .goods-left .goods .goods-item.border-line:after{
	content: '';
	position: absolute;
	bottom: 0px;
	left: 5px;
	height: 1px;
	width: 80%;
	background: rgba(7,17,27,0.1);
	transform: scaleY(0.5);
	-webkit-transform: scaleY(0.5);
}
.good .goods-left .goods .goods-item .text{
	display: table-cell;
	line-height: 14px;
	vertical-align: middle;
	width: 56px;
	padding: 0 12px;
	font-size: 12px;
}
.good .goods-center{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.goods-center .goods-name{
	height: 26px;
	font-size: 12px;
	padding-left: 14px;
	color: rgb(147,153,159);
	line-height: 26px;
	background: #f3f5f7;
	border-left:2px solid #d9dde1;
}
.goods-center .icon{
	display: inline-block;
	vertical-align: top;
}
.goods-center .icon img{
	border-radius: 4px;
}
.goods-center .food-item{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 18px;
	font-size: 0;
	padding-bottom: 18px;
}
.goods-center .food-item.food-line{
	position: relative;
}
.goods-center .food-item.food-line:after{
	content: '';
	position: absolute;
	bottom: 0px;
	left: 0px;
	height: 1px;
	width: 100%;
	background: rgba(7,17,27,0.1);
	transform: scaleY(0.5);
	-webkit-transform: scaleY(0.5);
}
.goods-center .content{
	font-size: 0px;
	padding-left:10px;
}
.goods-center .content .name{
	font-size: 14px;
	vertical-align: top;
	color: rgb(7,17,27);
	line-height: 14px;
	margin-bottom: 8px;
}
.goods-center .content .des{
	font-size: 10px;
	color: rgb(147,153,159);
	line-height: 12px;
	margin-bottom: 8px;
}
.goods-center .content .sell{
	font-size: 10px;
	color: rgb(147,153,159);
	line-height: 10px;
	margin-bottom: 8px;
}
.goods-center .content .sell .rating{
	margin-left: 12px;
}
.goods-center .content .price{
	font-size: 14px;
	line-height: 24px;
}
.goods-center .content .cart-wapper{
	position: absolute;
	bottom:6px;
	right: 0px;
}
.goods-center .content .price .now{
	color: rgb(240,20,20);
	font-weight: 700;
	margin-right: 8px;
}
.goods-center .content .price .old{
	color: rgb(147,153,159);
	text-decoration: line-through;
	font-size: 10px;
	font-weight:normal;
}



