
#app .tab{
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
#app .tab:after{
  content: '';
  width:100%;
  height: 1px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  background-color: rgba(7,17,27,0.1);
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
}
#app .tab .tab-item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app .tab .tab-item a{
  font-size: 14px;
  display: block;
  color: rgb(77,85,93);
}
#app .tab .tab-item .active{
  color: rgb(240,20,20);
}



